.footer {
    margin-top: auto;
    background-image: url("/foni.jpg");
    background-position: left;
    background-size: cover;
    background-repeat: no-repeat;
    color: #880e4f;
    padding: 40px 16px;
    text-align: center;
  }
  
  .title {
    color: black;
    font-weight: 700;
    font-family: serif;
    letter-spacing: 2px;
  }
  
  .link {
    gap: 8px;
    height: 20px;
    padding-top: 16px;
    font-size: 40px;
    color: black;
    text-decoration: none;
  }
  
  .instagramIcon {
    font-size: 60px;
    padding: 8px;
  }
  
  .locationLink {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 8px;
    padding-top: 16px;
    color: black;
    text-decoration: none;
  }
  
  .locationIcon {
    font-size: 70px;
    padding: 8px;
  }
  
  .mapWrapper {
    display: flex;
    justify-content: center;
  }
  
  .iframe {
    border: 0;
    width: 250px;
    height: 250px;
  }
  